<template>
    <div class="container">
        <div class="mod-calculator">
            <div class="search">
                <input type="text" placeholder="对方的称谓" @input="bindInput"/>
                <button @click="bindConfirm">查找</button>
            </div>
            <div class="list">
                <template v-if="value">
                    <template v-if="items.length">
                        <div class="h1"><span>关系：</span></div>
                        <div v-for="(item,index) in items" :key="index">
                            <div class="item">{{item}}</div>
                        </div>
                    </template>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Chain',
        components: {
        },
        data(){
            return {
                value:'',
                items:[]
            }
        },
        computed: {

        },
        methods:{
            bindInput:function(e){
                var _ = this;
                window.console.log(e);
                _.value = e.target.value;
                _.items = [];
            },
            bindConfirm:function(){
                var _ = this;
                var value = _.value;
                 window.console.log(value);
                var result = window.relationship({text:value,type:'chain'});
                if(result.length){
                    _.items = result;
                }else{
                    _.items = ['未找到相应关系！'];
                }
            }
        },
        created(){
            
        }
    }
</script>

